export const exceptNames = {
  name: true,
  filters: true
};

export function processor(proto, $$key, options) {
  "use strict";

  const optionName = $$key.slice(2);
  const addValue = proto[$$key];
  if ( options[optionName] && !exceptNames[optionName] ) {
    console && console.warn && console.warn(`Property [${optionName}] will override options[${optionName}]`);
  }
  options[optionName] = addValue;

}

export function decorator(proto, key, descriptor) {

  proto[`$$${key}`] = descriptor.initializer();
  delete proto[key];
}